export default {
	data() {
		return {
			images: []
		}
	},
	onReady() {
		uni.request({
			url: 'https://cn.bing.com/HPImageArchive.aspx?format=js&idx=1&n=20',
			success: (result) => {
				console.log(result)
				if (result.statusCode == 200 && result.data) {
					this.images = result.data.images
				}
			}
		})
	}
}
